<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{$t('AssetTransfer')}}:{{ ItemData.Name}}
                        </h5>
                        <button v-on:click="closeTransfer" href="javascript:;" class="close"><i class="fa fa-times" aria-hidden="true"></i></button>
                    </div>
                    <div class="modal-body">
                        <div class="row">
                            <div class="col-sm-12 col-lg-12">
                                <div class="form-group">
                                    <label>Select Product</label>
                                    <div class="listing float-none">
                                        <div class="table-responsive">
                                            <table class="table table-bordered dt-responsive nowrap mt-0">
                                                <thead class="thead-bg">
                                                    <tr>
                                                        <th class="text-center"><div class="pullleft"><input class="chkItems" type="checkbox" id="chkAll" name="checkbox" /></div></th>
                                                        <th><span href="javascript:;" id="Item">{{$t('Product')}}</span></th>
                                                        <th><span href="javascript:;" id="UniqueName">{{$t('UniqueName')}}</span></th>
                                                        <th><span href="javascript:;" id="ItemCode">{{$t('ItemCode')}}</span></th>
                                                        <th><span href="javascript:;" id="AvailableQuantity">{{$t('AVAILABLE_ITEMS')}}</span></th>
                                                        <th style="min-width:200px;"><span href="javascript:;" id="Quantity">{{$t('DivideLeaveQuantity')}}</span></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <template v-for="(item,index) in TransferData" >
                                                    <tr class="insertedrow Records parentmain bg-gold" >
                                                        <td class="text-center">
                                                            <input class="chkItems recordsCheckItems" type="checkbox" :value="ItemData.RelatedInfoId" name="ParentRelatedInfoId" :data-RelateInfoId="item.RelatedInfoId" />
                                                        </td>
                                                        <td>                                                            
                                                            <span :title="item.CatalogName" class="spnCatalogName">{{ item.CatalogName }}</span>
                                                        </td>
                                                        <td>
                                                            <span :title="item.UniqueName" class="spnUniqueName">{{item.UniqueName}}</span>
                                                        </td>
                                                        <td>
                                                            <span :title="item.ItemCode" class="spnItemCode">{{ item.ItemCode }}</span>
                                                        </td>
                                                        <td>
                                                            {{ item.AvailableItems}}
                                                        </td>
                                                        <td>
                                                            <input v-if="item.ISUnique" type="text"  class="txtAvailableItems form-control" disabled :value="item.AvailableItems" :data-oldqty="item.AvailableItems" data-numberonly="yes">
                                                            <input v-else type="text" class="txtAvailableItems form-control" :value="item.AvailableItems" :data-oldqty="item.AvailableItems" data-numberonly="yes">
                                                        </td>
                                                    </tr>
                                                    <tr v-for="(child,childIndex) in index.AssetCatalogueGroup" :key="childIndex">
                                                        <td class="text-center">
                                                            <input class="chkItems recordsCheckItems" type="checkbox"  value:="ItemData.RelatedInfoId" />                                                           
                                                        </td>
                                                        <td></td>
                                                        <td>                                                        
                                                            <span :title="child.CatalogName">{{child.CatalogName}}</span>
                                                        </td>
                                                        <td>
                                                            <span :title="child.UniqueName">{{child.UniqueName}}</span>
                                                        </td>
                                                        <td>
                                                            <span :title="child.ItemCode">{{child.ItemCode}}</span>
                                                        </td>
                                                        <td>
                                                            <input v-if="item.ISUnique" type="text"  class="txtAvailableItems form-control" disabled>
                                                            <input v-else type="text" class="txtAvailableItems form-control" >
                                                        </td>
                                                    </tr>
                                                </template>
                                                    <tr class="row">
                                                        <td v-if="NoRecordfound" colspan="7" class="no_records no-record">{{$t('No_Record_Found')}}</td>
                                                    </tr>
                                               
                                                </tbody>
                                            </table>
                                        </div>
                                    </div> 
                                </div>
                            </div>
                        </div>                      
                        <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                        </dynamic-form>                        
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';
import { TreeSelectField } from 'tg-controls_cli/dist/myLib.common'
export default {
      components: {
        'tree-view':TreeSelectField
    },
    props: {
        ItemData:{
            type:Array,
            required:true
        }
    },
   data(){
    return{
        DataLoaded:0,
        NoRecordfound:false,
        isLoading:false,
        TransferData:[],
        FormSchema: [
                {
                    layoutType: "double",
                    Data: [                       
                    {
                        astype: "TreeSelectField",
                        label: this.$t('Location'),
                        name: "Location",
                        value: null,
                        placeholder: "Select Location",
                        config: {
                            options: [],
                            onChange: this.GetLocationWiseUsers,                                                                                
                        },
                    },
                    {
                        astype: "SelectField",
                        label: this.$t('Users'),
                        name: "Users",
                        value: "",
                        placeholder: "",
                        config: {
                            options: [],
                            
                        },
                    },
                    ]
                },
                {
                    layoutType: "single",
                    Data: [                       
                        {
                            astype: "TextAreaField",
                            label: this.$t('Remarks'),
                            name: "Remarks",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": false },
                            config: {
                                rows:2
                            }
                        },                      

                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.closeTransfer
                }
            ],
            LocationData:[],
            UserData:[],
            TransferData:[]
    }
   },
   async created(){      
        
       await this.GetLocationByCompanyId();
       await this.GetCompanyAssestCatalogsDetalis(); 
       await this.GetLocationWiseUsers();      
       this.DataLoaded=1;               
   },
    methods:{
        GetLocationByCompanyId:async function(){            
            var vm=this;
            var url=`reqForm=`;
           await DataService.GetLocationByCompanyId(url).then(response=>{  
                //debugger              
                vm.LocationData = JSON.parse(response.data).data;     
                vm.FindalLocationArray = [];           
                vm.LocationData.forEach((value) => {
                    var CheckIfExist = vm.FindalLocationArray.filter(function (el) {
                        return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                    });
                    if (CheckIfExist.length == 0) {
                        if (value.PARENT_LOCATION_ID == null) {
                            let locationId = value.COMPANY_LOCATION_ID
                            let rslt = vm.getChildLocation(locationId, value, vm.LocationData)
                            vm.FindalLocationArray.push(rslt)
                        }
                    }
                });               
                 (vm.FormSchema)[0].Data[0].config.options = vm.FindalLocationArray;                
            });              
           
        },
        getChildLocation: function (locationId, currentItem, data) {
            let ths = this;
            let treeArray = {};
            treeArray = {
                id: currentItem.COMPANY_LOCATION_ID,
                label: currentItem.LOCATION_NAME
            }
            let retrunArray = treeArray;
            var childArray = data.filter(function (el) {
                return el.PARENT_LOCATION_ID == locationId;
            });
            let finalChildArray = [];
             childArray.forEach((value) => {
                let ChildfolderId = value.COMPANY_LOCATION_ID
                var CheckIfExist1 = finalChildArray.filter(function (el) {
                    return el.COMPANY_LOCATION_ID == value.COMPANY_LOCATION_ID;
                });
                if (CheckIfExist1.length == 0) {
                    var CheckIfExist = data.filter(function (el) {
                        return el.PARENT_LOCATION_ID == ChildfolderId;
                    });
                    if (CheckIfExist != null && CheckIfExist.length > 0) {
                        let rslt = ths.getChildLocation(ChildfolderId, value, data);
                        finalChildArray.push(rslt);
                    } else {
                        treeArray = {
                            id: `${value.COMPANY_LOCATION_ID}`,
                            label: `${value.LOCATION_NAME}`
                        }
                        finalChildArray.push(treeArray);
                    }
                }
            });
            retrunArray.children = finalChildArray;
             return retrunArray;
        },
          GetLocationWiseUsers:async function(){                     
            var vm=this;
            var location=(vm.FormSchema)[0].Data[0].value==null? 0:(vm.FormSchema)[0].Data[0].value;
            debugger;
            var url=`locationId=${location}&moduleName=Asset`;
             await DataService.GetLocationWiseUsers(url).then(response=>{
                vm.UserData = response.data;            
                    vm.UserData.forEach((item, index) => {
                        (vm.FormSchema)[0].Data[1].config.options.push({
                            name: item.Text,                            
                            value: `${item.Value}`
                        })
                    });
            })
        },
        GetCompanyAssestCatalogsDetalis:function(){
            var url=`locationid=${this.ItemData.LocationId}&assetUniqueId=${this.ItemData.RelatedInfoId}&assetAssignmentId=${this.ItemData.AssetAssignmentId}`;
            DataService.GetCompanyAssestCatalog(url).then(response=>{                
                this.TransferData.push(JSON.parse(response.data));                             
            })
        },
        getData:function(){            
            var Data="";
            this.TransferData.forEach((value, index) => {
                var catalogId = this.ItemData.AssetCatalogueId;
                var relatedInfoId = value.RelatedInfoId;
                var quantity = value.AvailableItems;
                var assignmentId = value.AssetAssignmentId;
                if (Data == "") {
                        Data = catalogId + "," + relatedInfoId + "," + quantity + "," + assignmentId;
                    }
                else {
                    Data = Data + "|" + catalogId + "," + relatedInfoId + "," + quantity + "," + assignmentId;
                } 
            });
            return Data;
        },
        closeTransfer:function()
        {  
            this.$emit('closeTransfer')
        },
        onCancel:function(){
            this.$emit('closeTransfer')
        },
        onSubmit:function(formfields, val){    
            debugger        
            var vm=this;
            vm.isLoading=true;   
            var tranferData = vm.getData();
            if(tranferData == ""){
                vm.ShowAlert(vm.$t('PleaseSelectAtleastOneProduct'), "failure", true, vm.$t("Alert"));
                vm.isLoading=false;   
            }
            else{                
               let data={                     
                    transferData : tranferData, 
                    locationId : formfields.Location, 
                    userId: formfields.Users, 
                    comments:formfields.Remarks
                 }
                 var Jsondata=JSON.stringify(data);
                 DataService.AssetTransferToEmployee(Jsondata).then(response=>{                   
                    if(response.data.status == 0){
                        vm.isLoading=false;
                        vm.ShowAlert(response.data.msg, "success", true, vm.$t("Alert"));
                        vm.closeTransfer();
                        vm.$parent.FetchData();
                    }
                    else if(response.data.status == -1){
                        vm.isLoading=false;
                        vm.ShowAlert(response.data.msg, "failure", true, vm.$t("Alert"));
                    }else{
                        vm.isLoading=false;
                        vm.ShowAlert(vm.$t('ErrorWhileProcessingRequest'), "failure", true, vm.$t("Alert"));
                    }
                });
            }
        }
    }
}
</script>