<template>
<section>
    <loader :is-visible="isLoading"></loader>
    <tg-list :IsShowAction="true" :listType="['List']" ModuleName="Asset" SubModuleCode="Asset" IdentityColumn="AssetCatalogueId" HeaderText="My Products" :ListData="MyItemList" :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy" v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata" :NorecordfoundText="$t('NoRecordfound')">
        <template v-slot:slotdata="{ data }">
            <template v-if="data.column.COLUMN_NAME == 'BarcodeNumber'">
                <div class="form-control p-0 border-0 text-center">
                    <img :src='data.row.BarcodeImageUrl' style="max-height:40px;" />
                    <span class="d-block w-100 text-center">{{ data.row.BarcodeNumber }}</span>
                </div>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'AttachmentPath'">
                <div class="form-control p-0 border-0 text-center">
                    <img :src='data.row.AttachmentPath' style="max-height:80px;" />
                </div>
            </template>
            <template v-if="data.column.COLUMN_NAME == 'NumberOfItems'">
                <span class="text-dark" :title="data.column.COLUMN_NAME">
                    <i class="fa fa-list-ul" aria-hidden="true"></i><span class="noti-circle noti-green">{{
                        data.row.NumberOfItems }}</span>
                </span>
            </template>
        </template>
    </tg-list>
    <checkAvailablityModal v-if="IscheckAvailablity" @ClosePOPuP="ClosePOPuP"></checkAvailablityModal>
    <RequestQuantity v-if="isRequestForQuantity" @CloseRequestForQuantity="CloseRequestForQuantity" :ItemData="ItemData"> </RequestQuantity>
    <RequestReplace v-if="isRequestForReplace" @closeRequestReplace="closeRequestReplace" :ItemData="ItemData" :Title="Title"> </RequestReplace>
    <Consume v-if="isConsume" @closeconsume="closeconsume" :ItemData="ItemData"> </Consume>
    <Transfer v-if="isTransferpopup" @closeTransfer="closeTransfer" :ItemData="ItemData"></Transfer>
    <SelfCheckOut v-if="isSelfCheckOut" @CloseSelfCheckOut="CloseSelfCheckOut" :ItemData="ItemData"></SelfCheckOut>
    <ShowTermsCondition v-if="IsShowTermsCondition" @CloseAcceptTermsConditions="CloseAcceptTermsConditions" :ItemData="ItemData"></ShowTermsCondition>
</section>
</template>

<script>
import DataService from '../../service/DataService'
import Schema from '../MyProduct/ListSchema'
import checkAvailablityModal from '../../components/Modal/checkAvailablity.vue'
import RequestQuantity from "../../components/Modal/RequestQuantity.vue"
import RequestReplace from "../../components/Modal/RequestReplace.vue"
import Consume from "../../components/Modal/Consume.vue"
import Transfer from "../../components/Modal/TransferAssetPopUp.vue"
import SelfCheckOut from "../../components/Modal/SelfCheckOut.vue"
import ShowTermsCondition from '../../components/Modal/ShowTermsConditions.vue'
import {
    DateTime
} from 'luxon'

export default {
    components: {
        checkAvailablityModal,
        RequestQuantity,
        RequestReplace,
        Consume,
        Transfer,
        SelfCheckOut,
        ShowTermsCondition
    },
    data() {
        return {
            UserId: null,
            CompanyId: null,
            IsDefault: null,
            MyItemList: [],
            abc: [],
            txtsearch: '',
            reqstatus: '',
            LocationId: '',
            sortBy: '',
            sortExp: '',
            requisitionId: '',
            GroupData: [],
            productName: '',
            noRecord: false,
            isRequestForQuantity: false,
            isRequestForReplace: false,
            isTransferpopup: false,
            isReturn: false,
            isConsume: false,
            PageNumber: 1,
            PageSize: 10,
            CurrentPage: 1,
            TotalPages: 0,
            IscheckAvailablity: false,
            IsCheckOutRequestList: false,
            IsAddCheckOutRequestList: false,
            widgets: [],
            listheaderbuttons: Schema.buttons,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            TotalRecords: 0,
            MyItemList: [],
            Headers: [{
                    "COLUMN_NAME": 'UniqueName',
                    "DATA_TYPE": "image",
                    "SORTABLE": true,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Product",
                    "DISPLAY_ORDER": 1,

                },
                {
                    "COLUMN_NAME": 'AttachmentPath',
                    "DATA_TYPE": "image",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Thumbnail",
                    "DISPLAY_ORDER": 2,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'AssetCategoryName',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Product Type",
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'AssignedFrom',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Assigned From",
                    "DISPLAY_ORDER": 4,

                },
                {
                    "COLUMN_NAME": 'AssignedTill',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Assigned Till",
                    "DISPLAY_ORDER": 5,

                },
                {
                    "COLUMN_NAME": 'BarcodeNumber',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Barcode",
                    "DISPLAY_ORDER": 6,
                    settings: {
                        isInSlot: true,
                    },
                },
                {
                    "COLUMN_NAME": 'NumberOfItems',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": "Quantity",
                    "DISPLAY_ORDER": 7,
                    settings: {
                        isInSlot: true,
                    }

                }
            ],
            LegendArray: [],
            SortExp: '',
            SortBy: '',
            ItemData: [],
            isLoading: false,
            isSelfCheckOut: false,
            Title: '',
            IsShowTermsCondition: false,
            UserDateTime: new Date(),
            selfTransfer: '',
            selfCheckout: ''
        }
    },
    created: function () {
        this.SelfCheckoutMethod();
        this.SelfTransferMethod();
        this.FetchData();
        this.IsDefault = this.GetUserInfo.IsDefault;
        this.LegendArray = [{
                class: 'legends_profile colorboxsquare',
                text: ('Pending Approval')
            },
            {
                class: 'legend-pending colorboxsquare',
                text: ('Requested for Return')
            },
            {
                class: 'legends_low colorboxsquare',
                text: ('Shared Products')
            },
            {
                class: 'legends_medium colorboxsquare',
                text: ('Container Products')
            }
        ]
    },
    methods: {
        FetchData: function () {
            var vueObj = this;
            vueObj.isLoading = true;
            var params = `pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&sortBy=${vueObj.SortBy}&SortExp=${vueObj.SortExp}&reqstatus=&requisitionId=&${vueObj.searchCondition}`
            DataService.MyItemListing(params).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {
                        vueObj.MyItemList = response.data;
                        vueObj.MyItemList.forEach((row) => {
                            row.additionalClass = '';
                            if (row.IsAccept == 0 && row.IsEmployeeAcceptance == 1) {
                                row.additionalClass = 'high-profile-bar';
                            }
                            if (row.IsShared == 1) {
                                row.additionalClass = 'low-bar';
                            }
                            if (row.ReturnRequestByPM == 1) {
                                row.additionalClass = 'high-bar';
                            }
                            if (row.IsContainer == 1) {
                                row.additionalClass = 'medium-bar';
                            }
                        });
                        vueObj.TotalRecords = response.data[0].TOTAL;
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;
                        vueObj.isLoading = false;
                    } else {
                        vueObj.MyItemList = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;
                        vueObj.isLoading = false;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.MyItemList = [];
                    vueObj.TotalRecords = 0
                    vueObj.isLoading = false;
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap()
                }, 100);
                setTimeout(function () {
                    vueObj.ResponsiveDataTable("tablelistingdata");
                }, 500);
            });
        },
        SelfTransferMethod: function () {
            let url = `moduleName=Asset&configkey=SelfTransfer`;
            DataService.UserSelfCheckout(url).then(response => {
                this.selfTransfer = response.data;
            });
        },
        SelfCheckoutMethod: function () {
            let url = `moduleName=Asset&configkey=SelfCheckout`;
            DataService.UserSelfCheckout(url).then(response => {
                this.selfCheckout = response.data;
            });
        },
        SearchData: function () {
            this.PageNumber = 1;
            this.FetchData();
        },
        ResetData: function () {
            this.txtsearch = '';
            this.productName = '';
            this.FetchData();
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        },
        checkAvailablity: function () {
            this.IscheckAvailablity = true;
        },
        CheckOutRequestList: function () {
            this.$router.push({
                name: "ProductTypeRequest"
            });
        },
        AddCheckOutRequest: function () {
            this.$router.push({
                name: "AssetAssignmentCheckOut"
            });
        },
        ClosePOPuP: function () {
            this.IscheckAvailablity = false;
        },
        RenderRowActionMethod() {},
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        RequestQuantity: function (ths) {
            this.ItemData = ths;
            this.isRequestForQuantity = true;
        },
        CloseRequestForQuantity: function () {
            this.isRequestForQuantity = false;
        },
        RequestReplace: function (ths, Title) {
            this.ItemData = ths;
            this.isRequestForReplace = true;
            this.Title = Title == 'RequestReplace' ? 'Request Replace' : 'Return'
        },
        closeRequestReplace: function () {
            this.isRequestForReplace = false;
        },
        AssetReturn: function (ths) {
            this.ItemData = ths;
            this.isReturn = true;
        },
        closeReturn: function () {
            this.isReturn = false;
        },
        Consume: function (ths) {
            this.ItemData = ths;
            this.isConsume = true;
        },
        closeconsume: function () {
            this.isConsume = false;
        },
        Transfer: function (ths) {
            this.ItemData = ths;
            this.isTransferpopup = true;
        },
        closeTransfer: function () {
            this.isTransferpopup = false;
        },
        SelfCheckOut: function (ths) {
            this.ItemData = ths;
            this.isSelfCheckOut = true;
        },
        CloseSelfCheckOut: function () {
            this.isSelfCheckOut = false;
        },
        AcceptTermsConditions: function (ths) {
            this.ItemData = ths;
            this.IsShowTermsCondition = true;
        },
        CloseAcceptTermsConditions: function () {
            this.IsShowTermsCondition = false;
        },
        actionButtonClick: function (callbackfunction, item) {
            var vm = this;
            switch (callbackfunction) {
                case "Consume":
                    vm.Consume(item);
                    break;
                case "RequestQuantity":
                    vm.RequestQuantity(item);
                    break;
                case "RequestReplace":
                    vm.RequestReplace(item, 'RequestReplace');
                    break;
                case "Return":
                    vm.RequestReplace(item, 'Return');
                    break;
                case "SelfCheckOut":
                    vm.SelfCheckOut(item);
                    break;
                case "Transfer":
                    vm.Transfer(item);
                    break;
                case "Accept":
                    vm.IsAccept(item, 'Accept');
                    break;
                case "Reject":
                    vm.IsAccept(item, 'Reject');
                    break;
                case "AcceptTermsConditions":
                    vm.AcceptTermsConditions(item);
                    break;
            }
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "Availability":
                    this.checkAvailablity();
                    break;
                case "CheckOutRequestList":
                    this.CheckOutRequestList();
                    break;
                case "AddCheckOutRequests":
                    this.AddCheckOutRequest();
                    break;
                case "SelfAssignment":
                    this.SelfAssignment();
                    break;
            }
        },
        SelfAssignment: function () {
            this.$router.push({
                name: "AddAssetAssignment",
                params: {
                    reqForm: 'myitems'
                }
            });
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        renderActions(row) {         
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = {
                    ...actionItem
                };
                switch (action.title) {
                    case 'Consume':
                        if ((row.NumberOfItems > 0 && (row.IsConsumable) == 1) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {                            
                            if (vm.formatTimeToCustomFormat(row.AssignedFrom) <= vm.formatTimeToCustomFormat(new DateTime)) {
                                action.isDisabled = false;
                            } else {
                                action.isDisabled = true;
                            }

                        } else {
                            action.isDisabled = true;
                        }
                        break;

                    case 'Replace Request':
                        if ((row.NumberOfItems > 0) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;

                    case 'Self CheckOut':

                        if (((row.IsSelfCheckout == 1) && (row.ISUnique == false) && (this.selfCheckout == "yes")) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                            action.isDisabled = false;
                            action.isVisible = true;
                        } else {
                            action.isDisabled = true;
                            action.isVisible = false;
                        }

                        break;
                    case 'Request Quantity':

                        if (((row.IsSelfCheckout == 1) && (row.ISUnique == false) && (this.selfCheckout == "yes")) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                            action.isDisabled = true;
                            action.isVisible = false;
                        } else {
                            if ((row.ISUnique == false) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                                action.isDisabled = false;
                            } else {
                                action.isDisabled = true;
                            }
                        }
                        break;

                    case 'Transfer':
                        if (((row.NumberOfItems > 0) && ((row.IsTransferrable == 1) && (this.selfTransfer == "yes" || this.default == true))) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;

                    case 'Return':

                        if ((row.NumberOfItems > 0) && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                            if (row.IsAccept != 0 || row.IsSelfCheckout == 1) {
                                action.isDisabled = false;
                            } else {
                                action.isDisabled = true;
                            }

                        } else {
                            if (row.IsAccept != 0) {
                                action.isDisabled = false;
                            } else {
                                action.isDisabled = true;
                            }
                        }
                        break;

                    case 'Accept':

                        if (row.IsEmployeeAcceptance != 1 || row.IsAccept != 1) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;

                    case 'Reject':
                        if (row.IsEmployeeAcceptance == 1 || row.IsAccept != 1) {
                            action.isDisabled = false;
                        } else {
                            action.isDisabled = true;
                        }
                        break;
                    case "Accept Terms Conditions":
                        if ((row.NumberOfItems) > 0 && (row.IsEnableTerms) == 1 && (row.IsEmployeeAcceptance == 1 ? row.IsAccept == 1 : row.IsAccept == 0)) {
                            action.isDisabled = false;
                            action.isVisible = true;
                        } else {
                            action.isDisabled = true;
                            action.isVisible = false;
                        }

                        break
                    default:
                        action.isVisible = false;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        IsAccept: function (ths, code) {
            var dataArray = [];
            var vm = this;
            vm.isLoading = true;
            var objsList = {
                RelatedInfoId: ths.RelatedInfoId,
                AssetAssignmentDetailId: ths.AssetAssignmentDetailId,
                AssetAssignmentId: ths.AssetAssignmentId,
                AcceptanceType: code == 'Accept' ? 1 : 2,
                EmployeeTransfer: ""
            };
            dataArray.push(objsList);
            DataService.SaveIsAccept(dataArray).then(response => {
                if (response.data.status == 0) {
                    vm.isLoading = false;
                    vm.ShowAlert(response.data.msg, "success", true, vm.$t('Alert'));
                    vm.FetchData();
                } else {
                    vm.isLoading = false;
                    vm.ShowAlert(response.data.msg, "failure", true, vm.$t('Alert'));
                }
            });
        },
        formatTimeToCustomFormat(inputTime) {
            if (inputTime != null && inputTime != '') {
                const inputLuxonDateTime = DateTime.fromISO(inputTime, {
                    zone: 'utc'
                }); // Assume the input time is in UTC
                const targetTimeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;

                const outputLuxonDateTime = inputLuxonDateTime.setZone(targetTimeZone);

                const formattedDateTime = outputLuxonDateTime.toFormat("MM/dd/yyyy h:mm a");
                return formattedDateTime;
            } else {
                return "--"
            }

        }
    }
}
</script>

<style>
span#action-list {
    display: flex;
    align-items: center;
}

.vue-treeselect__input {
    border: 1px solid #abb5bf;
    padding: 4px 12px;
}

.vue-treeselect__single-value {
    padding-left: 12px;
}
</style>
