<template lang="en">
<div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
        <div class="modal-dialog modal-dialog-centered">
            <div class="modal-content">
                <div class="modal-header">
                    <h5 class="modal-title">
                        {{Title}}:
                        {{ ItemData.Name }}
                    </h5>
                    <button v-on:click="closeRequestReplace" type="button" class="close" data-original-title="Close"><em class="fa fa-times"></em></button>

                </div>
                <div class="border p-3">
                    <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>
                </div>
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService';
export default {
    props: {
        ItemData: {
            type: Array,
            required: true
        },
        Title: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            IsShowAddNamePopup: false,
            Title: '',
            isLoading: false,
            FormSchema: [{
                    layoutType: "double",
                    Data: [{
                            astype: "TextField",
                            label: this.$t('Product'),
                            name: "Product",
                            value: "",
                            placeholder: "",
                            config: {
                                options: [],

                            },
                        },
                        {
                            astype: "TextField",
                            label: this.$t('Unique Name'),
                            name: "10/12/2022 12:AM",
                            value: "",
                            placeholder: "",

                        },
                        {
                            astype: "DateField",
                            label: this.$t('AssignedFrom'),
                            name: "10/12/2022 12:AM",
                            value: '',
                            placeholder: "10/12/2022 12:AM",

                            config: {
                                format: "MM/DD/YYYY"
                            }
                        },

                        {
                            astype: "DateField",
                            label: this.$t('Assigned Till'),
                            name: "Assigned Till",
                            value: '',
                            placeholder: "M/D/YYYY",

                            config: {
                                format: "MM/DD/YYYY"
                            }
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Reason'),
                            name: "Reason",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": true,
                                max: 250
                            },
                            config: {
                                options: []
                            },

                        },
                        {
                            astype: "TextField",
                            label: this.$t('Number of Products'),
                            name: "NumberofProducts",
                            value: "",
                            placeholder: "",
                            validationRules: {
                                "required": false,
                                max: 250
                            },
                        }
                    ]
                },
                {
                    layoutType: "single",
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Remarks'),
                        name: "Remarks",
                        value: "",
                        placeholder: "",
                        config: {
                            rows: 5
                        }
                    }, ]
                }

            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.onCancel
                }
            ],
            UserId: ''
        }
    },
    created: function () {
        this.UserId = this.GetUserInfo.ID;
        this.FetchData();
        this.GetAssetItemStatusList();
    },
    methods: {
        FetchData: function () {
            (this.FormSchema)[0].Data[0].value = this.ItemData.Name;
            (this.FormSchema)[0].Data[1].value = this.ItemData.UniqueName;
            (this.FormSchema)[0].Data[2].value = this.ItemData.AssignedFrom;
            (this.FormSchema)[0].Data[3].value = this.ItemData.AssignedTill;
            (this.FormSchema)[0].Data[5].value = this.ItemData.NumberOfItems;
        },
        GetAssetItemStatusList: function () {
            var url = `requestFrom=AssetReplace`
            DataService.GetAssetItemStatusList(url).then(response => {
                response.data.forEach((item) => {
                    (this.FormSchema)[0].Data[4].config.options.push({
                        value: item.STATUS_ID,
                        name: item.STATUS_NAME
                    })
                })
            });
        },
        closeRequestReplace: function () {
            this.$emit('closeRequestReplace')
        },
        onCancel: function () {
            this.$emit('closeRequestReplace')
        },
        onSubmit: function (formfields, val) {
            var vm = this;
            vm.isLoading = true;
            let JsonObj = {
                Name: vm.ItemData.Name,
                UniqueName: vm.ItemData.UniqueName,
                AssignedFrom: vm.ItemData.AssignedFrom,
                AssignedTill: vm.ItemData.AssignedTill,
                NumberOfItems: formfields.NumberofProducts,
                Reason: formfields.Reason,
                Remarks: formfields.Remarks,
                AssetReplacementType: vm.ItemData.AssetReplacementType,
                RelatedInfoId: vm.ItemData.RelatedInfoId,
                AssetAssignmentId: vm.ItemData.AssetAssignmentId,
                AssetAssignmentDetailId: vm.ItemData.AssetAssignmentDetailId,
                UserId: vm.UserId,
                AssetCatalogueId: vm.ItemData.AssetCatalogueId,
                RetrunStatus: vm.ItemData.RetrunStatus
            };
            var Jsondata = JSON.stringify(JsonObj);
            if (vm.Title == 'Return') {
                DataService.AssetReturn(Jsondata).then(response => {
                    if (response.data != null) {
                        if (response.data.status == 0) {
                            vm.ShowAlert(vm.$t('ReturnRequest'), "success", true, vm.$t('Alert'))
                            vm.onCancel();
                            vm.$parent.FetchData();
                            var final = Number(formfields.NumberofProducts) - vm.ItemData.NumberOfItems;
                            if (final < 0) {
                                setButonDisable(formfields.assetAssignmentId, final)
                            }
                        } else {
                            vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t('Alert'));
                            vm.onCancel();
                        };
                    }
                    vm.isLoading = false;
                });
            } else {

                DataService.AssetReplacement(Jsondata).then(response => {
                    if (response.data != null) {
                        if (response.data.status == 0) {
                            vm.ShowAlert(vm.$t('ReplaceRequest'), "success", true, vm.$t('Alert'))
                            vm.onCancel();
                            vm.$parent.FetchData();
                            var final = Number(formfields.NumberofProducts) - vm.ItemData.NumberOfItems;
                            if (final < 0) {
                                setButonDisable(formfields.assetAssignmentId, final)
                            }
                        } else {
                            vm.ShowAlert(vm.$t("ErrorWhileProcessingRequest"), "failure", true, vm.$t('Alert'));
                            vm.onCancel();
                        };
                    }
                    vm.isLoading = false;
                });
            }
        }
    },

}
</script>
